	module.exports = {"data":{"site":{"siteMetadata":{"title":"Phanor Coll - Fullstack Developer","author":"Phanor Coll"}}},"pathContext":{"posts":[{"html":"<h2>Forrester predice que mas del 25% de todas las empresas usaran una base de datos de grafos para el 2017</h2>\n<p>Con un poco mas de dos años estudiando y trabajando con Neo4j, he descubierto su versatilidad, su rapidez y beneficios, llevandome a ser editor tecnico del libro Neo4j Cookbook, participar en comunidades a nivel internacional, a dar charlas en eventos tecnologicos y a crear el grupo de usuarios Neo4j en la ciudad de Merida, Venezuela.</p>\n<p>Sin darme cuenta me vi rodeado de expertos en el area que sin pensarlo dos veces me han transmitido sus conocimientos sobre un mundo lleno de grafos. El formar parte de una comunidad sin egos, con personas que quieren transmitir el amor por lo que hacen y enseñar, hacen que valgan la pena las largas noches leyendo, escribiendo codigo, instalando, probando y lo mas importante CREANDO.</p>\n<p>El dia de hoy recibi la carta de aceptacion al programa de socios de Neo4j, lo cual me permitira avanzar en mi crecimiento con esta tecnologia y asi poder transmitirla a todos los que quieran conocer, aprender y usar Neo4j.</p>\n<p>Gracias el equipo de Neo4j por aceptarme como miembro en una creciente comunidad.</p>\n<blockquote>\n<p>\"Life is a graph, embrace it\"</p>\n</blockquote>","id":"/home/development/projects/reactjs/phanorcoll.com/gatsby-blog/src/pages/blog/2018-06-07-neo4j-partner/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2014-06-07","path":"/neo4j-partner","title":"Aceptado en el programa NEO4J PARTNER PROGRAM","excerpt":"Vivimos en un mundo donde de la noche a la mañana la cantidad de informacion puede crecer a un punto donde no se puede manejar y donde las soluciones relacionales dejan de ser la mejor opcion.","tags":["neo4j","partner program","graph"],"featuredImage":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":1.4789156626506024,"sizes":"(max-width: 491px) 100vw, 491px","src":"/static/neo4j-c9ad91efe57cd386c72acc02e4f7cb4b-1c558.png","srcSet":"/static/neo4j-c9ad91efe57cd386c72acc02e4f7cb4b-e7e2a.png 123w,\n/static/neo4j-c9ad91efe57cd386c72acc02e4f7cb4b-fed6a.png 246w,\n/static/neo4j-c9ad91efe57cd386c72acc02e4f7cb4b-1c558.png 491w"}}}}}],"tagName":"graph"}}

